<template>
    <el-main>
        <el-row>
            <el-form
                :inline="true"
                :model="marketTeacher"
                label-position="right"
                class="demo-form-inline"
                label-width="75px"
                size="small"
            >
                <el-form-item label=":">
                    <el-input v-model="marketTeacher.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label=":">
                    <el-select v-model="marketTeacher.orgCode" placeholder="">
                        <el-option
                        v-for="item in orgList"
                        :key="item.orgCode"
                        :label="item.orgName"
                        :value="item.orgCode">
                        </el-option>
                    </el-select>
                </el-form-item>
                <!-- <el-form-item label=":">
                    <el-select v-model="marketTeacher.referrer" placeholder="">
                        <el-option label="" value="shanghai"></el-option>
                        <el-option label="" value="beijing"></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label=":">
                    <el-input v-model="marketTeacher.idCard" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label=":">
                    <el-input v-model="marketTeacher.homeAdd" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label=":">
                    <el-date-picker
                        v-model="marketTeacher.recordDate"
                        type="datetime"
                        placeholder=""
                    ></el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="checkMarket" class="market-teacher-check"></el-button>
                </el-form-item>
            </el-form>
        </el-row>
        <el-row class="market-teacher-table-operation">
            <el-button type="primary" @click="addTeacher"></el-button>
            <el-button type="primary" @click="inputTeacher"></el-button>
            <el-button type="primary" @click="outputTeacher"></el-button>
        </el-row>
        <el-table
            ref="multipleTable"
            :data="marketT"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
        >
            <el-table-column align="center" type="selection"></el-table-column>
            <el-table-column align="center" prop="name" label=""></el-table-column>
            <el-table-column align="center" prop="sex" label="">
                <template slot-scope="scope">{{scope.row.sex === 1 ? '' : ''}}</template>
            </el-table-column>
            <el-table-column align="center" label="" show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.orgCode}}</template>
            </el-table-column>
            <el-table-column align="center" prop="idCard" label="" width="120px"></el-table-column>
            <el-table-column align="center" label="" show-overflow-tooltip>
                <template
                    slot-scope="scope"
                >{{scope.row.graduateSchool ? scope.row.graduateSchool : '-'}}</template>
            </el-table-column>
            <el-table-column align="center" prop="homeAdd" label=""></el-table-column>
            <el-table-column
                align="center"
                prop="recordDate"
                label=""
                width="200px"
                show-overflow-tooltip
            >
                <template slot-scope="scope">
                    {{parseTime(scope.row.recordDate)}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small"></el-button>
                    <el-button type="text" size="small" @click="delTeacher(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="marketTeacher.pageIndex"
            :page-sizes="[5, 10, 20, 40]"
            :page-size="marketTeacher.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="marketT.length"
        ></el-pagination>
        <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form ref="addNewTeacher" :model="newTeacher" inline label-width="80px">
                <el-form-item
                    :rules="[{ required: true, message: ''}]"
                    label=""
                    prop="name"
                >
                    <el-input v-model="newTeacher.name"></el-input>
                </el-form-item>
                <el-form-item :rules="[{ required: true, message: ''}]" label="" prop="sex">
                    <el-radio v-model="newTeacher.sex" :label="1"></el-radio>
                    <el-radio v-model="newTeacher.sex" :label="0"></el-radio>
                </el-form-item>
                <el-form-item
                    :rules="[{ required: true, message: ''}]"
                    label=""
                    prop="idCard"
                >
                    <el-input v-model="newTeacher.idCard"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="newTeacher.graduateSchool"></el-input>
                </el-form-item>
                <el-form-item
                    :rules="[{ required: true, message: ''}]"
                    label=""
                    prop="homeAdd"
                >
                    <el-input v-model="newTeacher.homeAdd"></el-input>
                </el-form-item>
                <el-form-item
                    :rules="[{ required: true, message: ''}]"
                    label=""
                    prop="recordDate"
                >
                    <el-date-picker
                        v-model="newTeacher.recordDate"
                        type="datetime"
                        placeholder=""
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="newTeacher.referrer" placeholder="">
                        <el-option
                        v-for="item in marketT"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item
                    :rules="[{ required: true, message: ''}]"
                    label=""
                    prop="orgCode"
                >
                    <el-select v-model="newTeacher.orgCode" placeholder="">
                        <el-option
                        v-for="item in orgList"
                        :key="item.orgCode"
                        :label="item.orgName"
                        :value="item.orgCode">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="closeDailog('addNewTeacher')"> </el-button>
                <el-button type="primary" @click="sureAdd('addNewTeacher')"> </el-button>
            </div>
        </el-dialog>
    </el-main>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { parseTime, filterObjectemptyKey } from "@/utils";
import { MessageBox } from 'element-ui';
export default {
    data() {
        return {
            currentPage: 1,
            marketTeacher: {
                name: "",
                orgCode: "",
                referrer: "",
                idCard: "",
                homeAdd: "",
                recordDate: "",
                pageIndex: 1,
                pageSize: 5
            },
            dialogFormVisible: false,
            newTeacher: {
                name: "",
                sex: "",
                idCard: "",
                graduateSchool: "",
                homeAdd: "",
                recordDate: "",
                referrer: "",
                orgCode: ""
            }
        };
    },
    computed: {
        ...mapState({
            marketT: state => {
                return state.marketTeacher.marketTeacher;
            },
            orgList: state => {
                return state.marketTeacher.orgList;
            }
        })
    },
    mounted() {
        let newObj = filterObjectemptyKey(this.marketTeacher);
        this.getTeacher(newObj);
        this.getOrglist()
    },
    methods: {
        ...mapActions({
            getTeacher: "marketTeacher/getTeacher",
            getOrglist: "marketTeacher/getOrglist"
        }),
        parseTime,
        checkMarket() {
            let newObj = filterObjectemptyKey(this.marketTeacher);
            this.getTeacher({ ...newObj, pageIndex: 1, pageSize: 5 });
        },
        // 
        addTeacher() {
            this.dialogFormVisible = true;
        },
        closeDailog(formName) {
            this.dialogFormVisible = false;
            this.$refs[formName].resetFields();
        },
        // 
        sureAdd(addTeacher) {
            this.$refs[addTeacher].validate(valid => {
                if (valid) {
                    let newObj = filterObjectemptyKey(this.newTeacher);

                    this.$http
                        .post("/market_teacher/addTeacher", {...newObj, recordDate: parseTime(newObj.recordDate)})
                        .then(res => {
                            this.closeDailog(addTeacher);
                            let newObj = filterObjectemptyKey(this.marketTeacher);
                            this.getTeacher({ ...newObj, pageIndex: 1, pageSize: 5 });
                        });
                } else {
                    return false;
                }
            });
        },
        // 
        inputTeacher() {},
        // 
        outputTeacher() {},
        // table
        handleSelectionChange() {},
        // 
        handleSizeChange(size) {
            this.marketTeacher.pageSize = size;
            this.getTeacher({ ...this.marketTeacher});
        },
        // 
        handleCurrentChange(page) {
            this.marketTeacher.pageIndex = page;
            this.getTeacher({ ...this.marketTeacher});
        },
        // 
        delTeacher(id) {
            MessageBox.confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'error',
                center: true
            }).then(res => {
                this.$http
                .post("/market_teacher/delTeacher", {
                    id
                })
                .then(res => {
                    this.getTeacher();
                });
            })
            
        }
    }
};
</script>

<style scoped>
.market-teacher-check {
    float: right;
}
.market-teacher-table-operation {
    float: right;
    padding-top: 15px;
}
</style>

<style>
</style>








